/* Adjust homepage header to match child pages */
.admin-bar.home.blog.has-header-image,
.home.blog.has-header-image,
.home.has-header-image {
    // Adjust header height
    .custom-header {
        height: auto;
        @include media-breakpoint-up(sm) {
            margin-bottom:74px!important;
        }
    }
    // Adjust header height
    .custom-header-media {
        @include media-breakpoint-up(sm) {
            height: 364px;
        }
    }
    // Prevent parallax scrolling
    .custom-header-media img {
        position: absolute;
        bottom: 0;
    }
    // Hide scroll down arrow
    .menu-scroll-down {
        display: none;
    }
    // Vertical center logo
    .site-branding {
        margin-bottom: 0!important;
    }
    // Darkening of background image
    .custom-header-media:before {
        background: -moz-linear-gradient(to top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 75%, rgba(0, 0, 0, 0.3) 100%);
        background: -webkit-linear-gradient(to top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 75%, rgba(0, 0, 0, 0.3) 100%);
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.3) 75%, rgba(0, 0, 0, 0.3) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00000000", endColorstr="#4d000000", GradientType=0);
        bottom: 0;
        content: "";
        display: block;
        height: 100%!important;
        left: 0;
        position: absolute;
        right: 0;
        z-index: 2;
    }
}